header {
  max-height: 70px;
  width: 100%;
  height: 8svh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  position: relative;
  font-size: 2em;
  border-bottom: 1px solid black;
  position: fixed;
  top: 0;

  nav{
    width: 100%;
    padding: 0 10px;
    max-width: 1100px;
    margin: 0 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: inherit;

    h1 {
      font-size: 26px;
    }
  }

  #logout, #login {
    font-size: 14px;
    font-family: "Inter", sans-serif;
    font-weight: bold;
    cursor: pointer;
    a {
      text-decoration: none;
      color: black;
    }
  }

  #logo {
    font-family: $brand-font;
    color: $brand-colour;
    width: 50%;
    margin: 0;
    a {
      margin: 0;
      color: black;
    }
  }

  #nav-search {
    height: inherit;
    display: flex;
    justify-content: space-between;
    align-items: center;

    button {
      height: calc(75%);
      border: none;
      background-color: white;
      margin-left: 10px;

      svg {
        height: inherit;
      }
    }
  }
}